import React, { useState  } from 'react';

// components
import { CreateRecipeForm, UpdateRecipeForm } from './VetForms';


function RecipesList(props) {
  const { recipes, setRecipeCloseEdit, activeId } = props
  const recipeClass = 'mb-1 border-bottom font-16'

  return (
    recipes.lenght > 0
      ? <p className='mb-0'> </p>
      : recipes.map(recipe => 
          <p className={activeId === recipe.id ? recipeClass + ' bg-green2-light' : recipeClass} 
            onClick={() => setRecipeCloseEdit(recipe)}>
            {recipe.id} {recipe.drug_name} {recipe.ru_type} {recipe.ru_method} {recipe.doze}
          </p>
        )
  )
}

function RecipeDetailOrEdit (props) {
  const { recipe, drugs, med_methods, med_types, submitUpdateRecipe, edit, setEdit, submitDeleteRecipe } = props
  return (
    <div className='card card-style mx-0 mt-2'>
      <div className='content'>
        <p className='mb-0 font-16'>
            {recipe.id} {recipe.drug_name} {recipe.ru_type} {recipe.ru_method} {recipe.doze}
        </p>
        {edit 
          ? <UpdateRecipeForm 
              initialValues={{
                id: recipe.id,
                drug: recipe.drug,
                med_type: recipe.med_type,
                med_method: recipe.med_method,
                doze: recipe.doze,
              }}
              parentSubmit={submitUpdateRecipe}

              drugs={drugs}
              med_types={med_types}
              med_methods={med_methods}

              setEdit={setEdit}
            />
          : <div className='my-2'>
              <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 
                shadow-s bg-green2-light text-wrap mr-3' 
                onClick={() => setEdit(true)}>
                
              </button>
              <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 
                shadow-s bg-red2-light text-wrap mx-3' 
                onClick={() => submitDeleteRecipe(recipe.id)}>
                
              </button>
            </div>
        }
      </div>
    </div>
  )
}


export function RecipesTab (props) {
  const { recipes, drugs, submitCreateRecipe, submitUpdateRecipe, submitDeleteRecipe,
      eventFetching, eventError, message } = props
  const med_types = [
    {value: 'vac', label: ''},
    {value: 'prev', label: ''},
    {value: 'heal', label: ''},
  ]
  const med_methods = [
    // {value: null, label: ''},
    {value: 'feed', label: ''},
    {value: 'water', label: ''},
    {value: 'inj', label: ''},
  ]
  const [recipe, setRecipe] = useState()
  const [ edit, setEdit] = useState(false)

  function setRecipeCloseEdit (recipe){
    setEdit(false)
    setRecipe(recipe)
  }

  function submitUpdateRecipeCloseEdit () {
    submitUpdateRecipe()
    setEdit(false)
  }

  function submitDeleteRecipeSetNull (id) {
    submitDeleteRecipe(id)
    setRecipe(null)
  }

  return (
    <div className='card card-style mt-1'>
      <div className='content'>
        <h5></h5>
        <div className='row'>
          <div className='col-4'>
            <RecipesList recipes={recipes} setRecipeCloseEdit={setRecipeCloseEdit} 
              activeId={recipe ? recipe.id : 0}/>
          </div>
          <div className='col-8'>
            <CreateRecipeForm 
              parentSubmit={submitCreateRecipe} 
              drugs={drugs} 
              med_types={med_types}
              med_methods={med_methods}

              eventFetching={eventFetching}
              eventError={eventError}
              message={message}
            />
            {recipe && 
              <RecipeDetailOrEdit 
                recipe={recipe} 
                submitUpdateRecipe={submitUpdateRecipeCloseEdit}
                drugs={drugs} 
                med_types={med_types}
                med_methods={med_methods}

                edit={edit}
                setEdit={setEdit}

                submitDeleteRecipe={submitDeleteRecipeSetNull}

                eventFetching={eventFetching}
                eventError={eventError}
                message={message}
              />
            }
          </div>
        </div>
      </div>
    </div>
  )
}