import { put, call } from 'redux-saga/effects';

import VeterinaryActions from '../redux-sauce/veterinary'

export function* getFiltersData(api, action) {
    try {
        let response = yield call(api.getFiltersData, action.payload);
        yield put(VeterinaryActions.getFiltersDataSuccess(response));
    } catch (err) {
        yield put(VeterinaryActions.getFiltersDataFail(err))
    }
}

