import React, { Component } from 'react';


function SowListItem(props) {
  const { sow } = props
  return (
    <p className='mb-0'>
      {sow.farm_id}({sow.sub_result_days.split(' ')[0]} , {sow.location}) 
    </p>
  )
}



class SowDowntimeReportComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      downtime_wait_days: 40,
      downtime_sem_days: 28,
      downtime_sup28_days: 7,
      downtime_sup35_days: 65,
      downtime_farr_days: 30,
      downtime_nurse_days: 21
    }
    this.setData = this.setData.bind(this);
    this.getDowntime = this.getDowntime.bind(this);
	}
  
  setData (e) {
    this.setState({
      ...this.state,
      [e.target.name]: e.target.value
    })
  }

  getDowntime (e) {
    const { 
      downtime_wait_days,
      downtime_sem_days,
      downtime_sup28_days,
      downtime_sup35_days,
      downtime_farr_days,
      downtime_nurse_days } = this.state
      
    this.props.getSowsDowntimeReport({
      downtime_wait_days: downtime_wait_days,
      downtime_sem_days: downtime_sem_days,
      downtime_sup28_days: downtime_sup28_days,
      downtime_sup35_days: downtime_sup35_days,
      downtime_farr_days: downtime_farr_days,
      downtime_nurse_days: downtime_nurse_days
    })
  }

  render() {

    const { sowsDowntime } = this.props

    return (
      <div className='card card-style mx-0 mt-2'>
        <div className='content'>
          <h5>   </h5>
          <button className='btn btn-m bg-mainDark-dark' onClick={this.getDowntime}></button>
          <table className='table table-responsive table-sm' style={{'line-height': '17px'}}>
            <thead >
              <th></th>
              <th> , , , </th>
              <th> 2</th>
              <th> 28</th>
              <th> 35</th>
              <th>, </th>
              <th></th>
            </thead>
            <tbody>
              <tr>
                <td> </td>
                <td>{sowsDowntime['wait'] && sowsDowntime['wait']['count_all']}</td>
                <td>{sowsDowntime['sem'] && sowsDowntime['sem']['count_all']}</td>
                <td>{sowsDowntime['sup28'] && sowsDowntime['sup28']['count_all']}</td>
                <td>{sowsDowntime['sup35'] && sowsDowntime['sup35']['count_all']}</td>
                <td>{sowsDowntime['farr'] && sowsDowntime['farr']['count_all']}</td>
                <td>{sowsDowntime['nurse'] && sowsDowntime['nurse']['count_all']}</td>
              </tr>
              <tr>
                <td> </td>
                <td>
                  <input type='number' name='downtime_wait_days' value={this.state.downtime_wait_days}
                    onChange={this.setData} style={{'width': '50px'}}/>
                </td>
                <td>
                  <input type='number' name='downtime_sem_days' value={this.state.downtime_sem_days}
                    onChange={this.setData} style={{'width': '50px'}}/>
                </td>
                <td>
                  <input type='number' name='downtime_sup28_days' value={this.state.downtime_sup28_days}
                    onChange={this.setData} style={{'width': '50px'}}/>
                </td>
                <td>
                  <input type='number' name='downtime_sup35_days' value={this.state.downtime_sup35_days}
                    onChange={this.setData} style={{'width': '50px'}}/>
                </td>
                <td>
                  <input type='number' name='downtime_farr_days' value={this.state.downtime_farr_days}
                    onChange={this.setData} style={{'width': '50px'}}/>
                </td>
                <td>
                  <input type='number' name='downtime_nurse_days' value={this.state.downtime_nurse_days}
                    onChange={this.setData} style={{'width': '50px'}}/>
                </td>
              </tr>
              <tr>
                <td>  </td>
                <td>{sowsDowntime['wait'] && sowsDowntime['wait']['downtime_sows'].length}</td>
                <td>{sowsDowntime['sem'] && sowsDowntime['sem']['downtime_sows'].length}</td>
                <td>{sowsDowntime['sup28'] && sowsDowntime['sup28']['downtime_sows'].length}</td>
                <td>{sowsDowntime['sup35'] && sowsDowntime['sup35']['downtime_sows'].length}</td>
                <td>{sowsDowntime['farr'] && sowsDowntime['farr']['downtime_sows'].length}</td>
                <td>{sowsDowntime['nurse'] && sowsDowntime['nurse']['downtime_sows'].length}</td>
              </tr>
              <tr>
                <td>   </td>
                <td className='text-nowrap'>
                  {sowsDowntime['wait'] && 
                    sowsDowntime['wait']['downtime_sows'].map(sow =>
                      <p className='mb-0'>
                        {sow.farm_id ? sow.farm_id : ''}({sow.sub_result_days.split(' ')[0]} , {sow.location})
                      </p>
                      )
                  }
                </td>
                <td className='text-nowrap'>
                  {sowsDowntime['sem'] && 
                    sowsDowntime['sem']['downtime_sows'].map(sow =>
                      <SowListItem sow={sow} />
                      )
                  }
                </td>
                <td className='text-nowrap'>
                  {sowsDowntime['sup28'] && 
                    sowsDowntime['sup28']['downtime_sows'].map(sow =>
                      <SowListItem sow={sow} />
                      )
                  }
                </td>
                <td className='text-nowrap'>
                  {sowsDowntime['sup35'] && 
                    sowsDowntime['sup35']['downtime_sows'].map(sow =>
                      <SowListItem sow={sow} />
                      )
                  }
                </td>
                <td className='text-nowrap'>
                  {sowsDowntime['farr'] && 
                    sowsDowntime['farr']['downtime_sows'].map(sow =>
                      <SowListItem sow={sow} />
                      )
                  }
                </td>
                <td className='text-nowrap'>
                  {sowsDowntime['nurse'] && 
                    sowsDowntime['nurse']['downtime_sows'].map(sow =>
                      <SowListItem sow={sow} />
                      )
                  }
                </td>
                
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}


export default SowDowntimeReportComponent