import React, { Component } from 'react';
import { connect } from 'react-redux';

// components
import CreateRecipesAndDrugsTab from '../components/Veterinary/CreateRecipesAndDrugsTab'
import VetEventsTab from '../components/Veterinary/VetEventsTab'

import { TabMenu }  from '../components/CommonComponents'

// actions
import VeterinaryActions from '../redux/redux-sauce/veterinary';
import { change, reset } from "redux-form";


class VeterinaryContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tabs: [
        {name: 'recipesAndDrugsTab', active: false,  title: '  '},
        // {name: 'createMedEventTab',  active: false, title: ' . '},
        {name: 'listMedEventTab',    active: true, title: ' .  '},
      ]
    }
    this.setTab = this.setTab.bind(this);
    this.getActiveTab = this.getActiveTab.bind(this);
  }

  componentDidMount() {
  }

  setTab (tab) {
    let { tabs } = this.state
    tabs.map((tb) => {
      tb.active = false
      if (tb.name === tab.name)
        tb.active= true
    })

    this.setState({
      tabs: tabs
    })
  }

  getActiveTab () {
    let { tabs } = this.state
    let activeTab = {}
    tabs.map(tb => {
      if (tb.active)
        activeTab = tb
    })

    return activeTab
  }  

  render() {
    const activeTab = this.getActiveTab()
    return (
      <div className="">
        <TabMenu 
          tabs={this.state.tabs} setTab={this.setTab} workshop={''} activeTab={activeTab}
          user={this.props.state.auth.user}
        />

        {activeTab.name === 'recipesAndDrugsTab' &&
          <CreateRecipesAndDrugsTab 
            recipes={this.props.state.veterinary.recipes}
            recipe={this.props.state.veterinary.recipe}

            drugs={this.props.state.veterinary.drugs}
            drug={this.props.state.veterinary.drug}

            getRecipes={this.props.getRecipes}
            createRecipe={this.props.createRecipe}
            updateRecipe={this.props.updateRecipe}
            deleteRecipe={this.props.deleteRecipe}

            getDrugs={this.props.getDrugs}
            createDrug={this.props.createDrug}
            updateDrug={this.props.updateDrug}
            deleteDrug={this.props.deleteDrug}

            listFetching={this.props.state.veterinary.listFetching}
            errorList={this.props.state.veterinary.errorList}
            eventFetching={this.props.state.veterinary.eventFetching}
            eventError={this.props.state.veterinary.eventError}
            message={this.props.state.veterinary.message}

            createRecipeForm={this.props.state.form.createRecipeForm}
            updateRecipeForm={this.props.state.form.updateRecipeForm}

            createDrugForm={this.props.state.form.createDrugForm}
            updateDrugForm={this.props.state.form.updateDrugForm}

            resetEM={this.props.resetEM}
          />
        }

        {activeTab.name === 'listMedEventTab' &&
          <VetEventsTab 
            getEvents={this.props.getPigletsEvents}
            vetEvents={this.props.state.veterinary.pigletsVetEvents}

            getFiltersData={this.props.getFiltersData}
            filtersData={this.props.state.veterinary.filtersData}
            pveFormSetSections={this.props.pveFormSetSections}
            form={this.props.state.form.pveFilterForm}

            deletePigletsEvent={this.props.deletePigletsEvent}

            eventFetching={this.props.state.veterinary.eventFetching}
            listFetching={this.props.state.veterinary.listFetching}
            eventError={this.props.state.veterinary.eventError}
            message={this.props.state.veterinary.message}
            resetEM={this.props.resetEM}
          />
        } 

      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  state: state
})

const mapDispatchToProps = (dispatch) => ({
  getRecipes: query => dispatch(VeterinaryActions.getRecipesRequest(query)),
  createRecipe: query => dispatch(VeterinaryActions.createRecipeRequest(query)),
  updateRecipe: query => dispatch(VeterinaryActions.updateRecipeRequest(query)),
  deleteRecipe: query => dispatch(VeterinaryActions.deleteRecipeRequest(query)),

  getDrugs: query => dispatch(VeterinaryActions.getDrugsRequest(query)),
  createDrug: query => dispatch(VeterinaryActions.createDrugRequest(query)),
  updateDrug: query => dispatch(VeterinaryActions.updateDrugRequest(query)),
  deleteDrug: query => dispatch(VeterinaryActions.deleteDrugRequest(query)),

  getPigletsEvents: query => dispatch(VeterinaryActions.getPigletsEventsRequest(query)),
  updatePigletsEvent: query => dispatch(VeterinaryActions.updatePigletsEventRequest(query)),
  deletePigletsEvent: query => dispatch(VeterinaryActions.deletePigletsEventRequest(query)),

  getFiltersData: query => dispatch(VeterinaryActions.getFiltersDataRequest(query)),
  pveFormSetSections: ids => dispatch(change( "pveFilterForm", "sections", ids )),

  resetEM: () => dispatch(VeterinaryActions.resetErrorsAndMessages())
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(VeterinaryContainer);
