import axios from 'axios';
import endpoints from './endpoints';
import { parseErrorData, createUrlParamsFromFilters } from './utils';

const create = () => {
    const getFiltersData = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.FILTERS_DATA, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const getRecipes = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.RECIPES, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const getDrugs = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.DRUGS, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const getPigletsEvents = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.PIGLETS_VET_EVENTS, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const createRecipe = payload => {
        const { med_type, med_method, drug, doze } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.RECIPES;

        const formData = new FormData();
        formData.append("med_type", med_type);
        formData.append("med_method", med_method);
        doze && formData.append("doze", doze);
        formData.append("drug", drug);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const createDrug = payload => {
        const { name } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.DRUGS;

        const formData = new FormData();
        formData.append("name", name);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const updateRecipe = payload => {
        const { id, med_type, med_method, drug, doze } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.recipeDetail(id);

        const formData = new FormData();
        formData.append("med_type", med_type);
        formData.append("med_method", med_method);
        doze && formData.append("doze", doze);
        formData.append("drug", drug);
        
        return axios({
                    method: 'patch',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const updateDrug = payload => {
        const { id, name } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.drugDetail(id);

        const formData = new FormData();
        formData.append("name", name);
        
        return axios({
                    method: 'patch',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const updatePigletsEvent = payload => {
        const { id, date, recipe } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.pigletsVetEventDetail(id);

        const formData = new FormData();
        formData.append("date", date);
        formData.append("recipe", recipe);
        
        return axios({
                    method: 'patch',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const deleteRecipe = payload => {
        const { id } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.recipeDetail(id);

        return axios({
                    method: 'delete',
                    url: url,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const deleteDrug = payload => {
        const { id } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.drugDetail(id);

        return axios({
                    method: 'delete',
                    url: url,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const deletePigletsEvent = payload => {
        const { id } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.pigletsVetEventDetail(id);

        return axios({
                    method: 'delete',
                    url: url,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }
    

    return {
        getFiltersData,
        getRecipes,
        getDrugs,
        getPigletsEvents,
        createRecipe,
        createDrug,
        updateRecipe,
        updateDrug,
        updatePigletsEvent,
        deleteRecipe,
        deleteDrug,
        deletePigletsEvent
    }
}

export default {
    create
}