import React, { useState  } from 'react';

// components
import { CreateDrugForm, UpdateDrugForm } from './VetForms';


function DrugList(props) {
  const { drugs, setDrugCloseEdit, activeId } = props
  const drugClass = 'mb-1 border-bottom font-16'

  return (
    drugs.lenght > 0
      ? <p className='mb-0'> </p>
      : drugs.map(drug => 
          <p className={activeId === drug.id ? drugClass + ' bg-green2-light' : drugClass} 
            onClick={() => setDrugCloseEdit(drug)}>
            {drug.id} {drug.name}
          </p>
        )
  )
}

function DrugDetailOrEdit (props) {
  const { drug, submitUpdateDrug, edit, setEdit, submitDeleteDrug } = props
  return (
    <div className='card card-style mx-0 mt-2'>
      <div className='content'>
        <p className='mb-0 font-16'>
            {drug.id} {drug.name}
        </p>
        {edit 
          ? <UpdateDrugForm 
              initialValues={{
                id: drug.id,
                name: drug.name,
              }}
              parentSubmit={submitUpdateDrug}
              setEdit={setEdit}
            />
          : <div className='my-2'>
              <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 
                shadow-s bg-green2-light text-wrap mr-3' 
                onClick={() => setEdit(true)}>
                
              </button>
              <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 
                shadow-s bg-red2-light text-wrap mx-3' 
                onClick={() => submitDeleteDrug(drug.id)}>
                
              </button>
            </div>
        }
      </div>
    </div>
  )
}


export function DrugsTab (props) {
  const { drugs, submitCreateDrug, submitUpdateDrug, submitDeleteDrug } = props

  const [drug, setDrug] = useState()
  const [ edit, setEdit] = useState(false)

  function setDrugCloseEdit (drug){
    setEdit(false)
    setDrug(drug)
  }

  function submitUpdateDrugCloseEdit () {
    submitUpdateDrug()
    setEdit(false)
  }

  function submitDeleteDrugSetNull (id) {
    submitDeleteDrug(id)
    setDrug(null)
  }

  return (
    <div className='card card-style'>
      <div className='content'>
        <h5>  </h5>
        <div className='row'>
          <div className='col-4'>
            <DrugList drugs={drugs} setDrugCloseEdit={setDrugCloseEdit} 
              activeId={drug ? drug.id : 0}/>
          </div>
          <div className='col-8'>
            <CreateDrugForm 
              parentSubmit={submitCreateDrug} 
            />
            {drug && 
              <DrugDetailOrEdit 
                drug={drug} 
                submitUpdateDrug={submitUpdateDrugCloseEdit}
                edit={edit}
                setEdit={setEdit}
                submitDeleteDrug={submitDeleteDrugSetNull}
              />
            }
          </div>
        </div>
      </div>
    </div>
  )
}