import React, { Component } from 'react';
import { ErrorOrMessage } from '../CommonComponents';


export class BottomExpand extends Component  {
  constructor(props) { 
    super(props);
  }
  render () {
  const { expand, clickExpand, eventError, message, eventFetching, label } = this.props
  
  return (
    <div className='card card-style fixed-bottom mx-1 my-1'>
      <div className='content'>
        <div className='' 
          onClick={clickExpand}>
          {expand 
            ? <p className='my-0 text-center'>
                <i className="fas fa-chevron-down "><span className='ml-1'></span></i> 
              </p>
            : [<p className='my-0 text-center'>
                <i className="fas fa-chevron-up ">
                  <span className='ml-1'>{label}</span>
                </i>
              </p>,
              <ErrorOrMessage error={eventError} message={message} fetching={eventFetching}
              className='mt-2 mb-0 mx-1 font-15 text-center' />]
            }
        </div>
        {(expand) &&
          this.props.children
        }
      </div>
    </div>
  )}
}

export function PigletsGroupInline (props) {
  const { piglets } = props
  let age = ''
  piglets && piglets.age.split(' ').length > 1 
    ? age = piglets.age.split(' ')[0]
    : age = 0
  
  return (
    <p className={props.className}>
       {piglets.quantity} | {age && age + ' '} | {piglets.week_tour}
    </p>
   )
 }

 export function PigletsGroupInlineMin (props) {
  const { piglets } = props
  let age = ''
  piglets && piglets.age.split(' ').length > 1 
    ? age = piglets.age.split(' ')[0]
    : age = 0
  
  return (
    <p className={props.className}>
      - {piglets.quantity} | {age && age + ' '} | {piglets.week_tour}
    </p>
   )
 }

 export function PigletsListElem (props) {
  const { piglets } = props
  let tour = piglets.week_tour.split(' 20')[0]
  let age = ''
  piglets && piglets.age.split(' ').length > 1 
    ? age = piglets.age.split(' ')[0]
    : age = 0

  return (
    <div className='content mx-1 my-1'>
      <p className='my-0'>- {piglets.quantity}</p>
      <p className='my-0'>{age} </p>
      <p className='my-0'>{tour}</p>
      {piglets.transfer_part_number && <p className='my-0'> {piglets.transfer_part_number}</p>}
    </div>
  )
}


export const WeighingDetail = (props) => (
  <table className='table table-sm'>
      <tbody>
      <tr>
          <td> </td><td>{props.weighingData.average_weight}</td>
      </tr>
      <tr>
          <td> </td><td>{props.weighingData.total_weight}</td>
      </tr>
      {props.weighingData.gilts_quantity &&
        <tr>
          <td></td><td>{props.weighingData.gilts_quantity}</td>
        </tr>
      }
      </tbody>
  </table>
)