import React, { Component } from 'react';

import { ErrorMessage, Message } from '../CommonComponents';


class WS1ImportSeminationTab extends Component {
   constructor(props) {
    super(props);
    this.state = {
      file: null,
    }
    this.setData = this.setData.bind(this);
    this.uploadFile = this.uploadFile.bind(this);
  }
  
  setData (e) {
    this.setState({
      ...this.state,
      [e.target.name]: e.target.files[0],
    })
  }

  uploadFile () {
    this.props.uploadFile({file: this.state.file})
  }

  render() {
    const { eventFetching, responseData, eventError, message } = this.props
    this.props.sowsResetErrorsAndMessages()
    return (
      <div className='workshop-content'>
        <h3>   </h3>
        <div className="input-group">
          <input type="file" name="file" onChange={this.setData}/>
          <div className="input-group-append">
            <button type="button" class="btn btn-success btn-block" onClick={this.uploadFile}></button>
          </div>
        </div>
        <div className='col'>
          {eventError && <ErrorMessage error={eventError}/>}
          {message && <Message message={message}/>}

        </div>
        <div>
          {responseData && 
            <div>
              <p>  {responseData.seminated_list_count}</p>
              <p> (   ) 
                {responseData.already_seminated_in_tour_count}</p>
              <p>   {responseData.sows_in_another_tour.length}. 
                     .</p>
              <table className='table table-sm'>
                <thead>
                  <th>ID</th>
                  <th></th>
                  <th></th>
                  <th></th>
                </thead>
                <tbody>
                  {responseData.sows_in_another_tour.map(sow =>
                    <tr>
                      <td>{sow.farm_id}</td>
                      <td>{sow.tour}</td>
                      <td>{sow.location}</td>
                      <td>{sow.status}</td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          }
        </div>
      </div>
    )
  }
}

export default WS1ImportSeminationTab