import { createReducer, createActions } from 'reduxsauce'
import Immutable from 'seamless-immutable'

import { getObjectbyId, lodashToggle } from '../../components/utils'

/* ------------- Types and Action Creators ------------- */

const { Types, Creators } = createActions({
    getFiltersDataRequest: ['payload'],
    getFiltersDataFail: ['payload'],
    getFiltersDataSuccess: ['payload'],

    getRecipesRequest: ['payload'],
    getRecipesFail: ['payload'],
    getRecipesSuccess: ['payload'],

    createRecipeRequest: ['payload'],
    createRecipeFail: ['payload'],
    createRecipeSuccess: ['payload'],

    updateRecipeRequest: ['payload'],
    updateRecipeFail: ['payload'],
    updateRecipeSuccess: ['payload'],

    deleteRecipeRequest: ['payload'],
    deleteRecipeFail: ['payload'],
    deleteRecipeSuccess: ['payload'],

    getDrugsRequest: ['payload'],
    getDrugsFail: ['payload'],
    getDrugsSuccess: ['payload'],

    createDrugRequest: ['payload'],
    createDrugFail: ['payload'],
    createDrugSuccess: ['payload'],

    updateDrugRequest: ['payload'],
    updateDrugFail: ['payload'],
    updateDrugSuccess: ['payload'],

    deleteDrugRequest: ['payload'],
    deleteDrugFail: ['payload'],
    deleteDrugSuccess: ['payload'],

    createVetEventPigletsRequest: ['payload'],
    createVetEventPigletsFail: ['payload'],
    createVetEventPigletsSuccess: ['payload'],

    getPigletsEventsRequest: ['payload'],
    getPigletsEventsFail: ['payload'],
    getPigletsEventsSuccess: ['payload'],

    updatePigletsEventRequest: ['payload'],
    updatePigletsEventFail: ['payload'],
    updatePigletsEventSuccess: ['payload'],

    deletePigletsEventRequest: ['payload'],
    deletePigletsEventFail: ['payload'],
    deletePigletsEventSuccess: ['payload'],

    resetErrorsAndMessages: null,
})

export const VeterinaryTypes = Types
export default Creators

/* ------------- Initial State ------------- */

export const INITIAL_STATE = Immutable({
    drugs: [],
    drug: null,

    recipes: [],
    recipe: null,

    pigletsVetEvents: [],
    pigletsVetEvent: null,

    filtersData: {},

    listFetching: false,
    errorList: null,
    eventFetching: false,
    eventError: null,
    message: '',
})

/* ------------- Selectors ------------- */

export const VeterinarySelectors = {
    getRecipes: state => state.veterinary.recipes,
    createRecipe: state => state.veterinary.recipe,
    updateRecipe: state => state.veterinary.recipe,

    getDrugs: state => state.veterinary.drugs,
    createDrug: state => state.veterinary.drug,
    updateDrug: state => state.veterinary.drug,
}

/* ------------- Reducers ------------- */
//resetErrorsAndMessages
export const resetErrorsAndMessages = (state, { payload }) => {
    return state.merge({ errorList: null, eventError: null, message: '' })
}

// filters data
export const getFiltersDataRequest = (state, { payload }) => {
    return state.merge({ listFetching: true, filtersData: {} })
}

export const getFiltersDataSuccess = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList: null, filtersData: payload })
}

export const getFiltersDataFail = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList:payload, filtersData: {} })
}

// Get recipes
export const getRecipesRequest = (state, { payload }) => {
    return state.merge({ listFetching: true, recipes: [] })
}

export const getRecipesSuccess = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList: null, recipes: payload })
}

export const getRecipesFail = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList:payload, recipes: [] })
}

// create recipes
export const createRecipeRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, recipe: null })
}

export const createRecipeSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, eventError: null, recipe: payload,
        recipes: [
            ...state.recipes,
            payload
        ], 
        message: ' .'
     })
}

export const createRecipeFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, eventError: payload, recipe: null })
}

// update recipes
export const updateRecipeRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, recipe: null })
}

export const updateRecipeSuccess = (state, { payload }) => {
    const recipeInList = getObjectbyId(state.recipes, payload.id)
    const index = state.recipes.indexOf(recipeInList)

    return state.merge({ eventFetching: false, eventError: null, recipe: payload,
        recipes: [
            ...state.recipes.slice(0, index),
            payload,
            ...state.recipes.slice(index + 1),
        ],
        message: ' .'
     })
}

export const updateRecipeFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, eventError: payload, recipe: null })
}

// delete recipes
export const deleteRecipeRequest = (state, { }) => {
    return state.merge({ eventFetching: true, recipe: null })
}

export const deleteRecipeSuccess = (state, { payload }) => {
    const recipeInList = getObjectbyId(state.recipes, payload.id)
    const index = state.recipes.indexOf(recipeInList)
    return state.merge({ eventFetching: false, eventError: null,
            recipes: [
                ...state.recipes.slice(0, index),
                ...state.recipes.slice(index + 1),
            ],
            message: ' .'
        })
}

export const deleteRecipeFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, eventError: payload, })
}

