import React, { Component } from 'react';
import { ErrorOrMessage } from '../CommonComponents';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import MenuItem from '@material-ui/core/MenuItem';


export function SowFarmIdFilter (props) {
  return (
    <TextField
      label={" "}
      className={'bg-white ' + props.className}
      name='farm_id_starts'
      value={props.value}
      onChange={props.setQuery}
      InputLabelProps={{
        className: 'font-13',
      }}
    />
      )
}


export function SowTourFilter (props){
  const { formClass, labelClass, options, label } = props
  return (
    <FormControl className={'bg-white '+ formClass}>
      <InputLabel className={'font-13 ' + labelClass}>{label}</InputLabel>
      <Select
        onChange={props.setQuery}
        className={'font-13 ' + labelClass}
        name='tour'
      >
        {options.map(option =>
            <MenuItem value={option.id}> {' '} {option.week_number} {option.year}</MenuItem>
            )}
      </Select>
    </FormControl>
  )
}

export function SowSemUsoundFilter (props) {
  const { formClass, labelClass, label } = props

  return (
    <FormControl className={'bg-white '+ formClass}>
      <InputLabel className={'font-13 ' + labelClass}></InputLabel>
      <Select
        onChange={props.setSeminatedSuporosStatus}
        className={'font-13 ' + labelClass}
        defaultValue={'status_title= 35'}
      >
        <MenuItem value='to_seminate=true'> ,  </MenuItem>
        <MenuItem value='farm_id_isnull=true'></MenuItem>
        <MenuItem value='status_title= 1'> 1</MenuItem>
        <MenuItem value='status_title= 2'> 2</MenuItem>
        <MenuItem value='status_title= 28'> 28</MenuItem>
        <MenuItem value='status_title= 35'> 35</MenuItem>
      </Select>
    </FormControl>
    )
}