import React, { Component } from 'react';

// components
import { SowSingle } from '../SowRepresentations'
import { ErrorOrMessage, LoadingMessage } from '../CommonComponents';
import { CullingSowForm } from './SowForms';
import { getDateTimeNow } from '../WorkshopThree/WS3SowFarrowTab';


class WSSowCullingTab extends Component {
   constructor(props) {
    super(props);
    this.state = {
      cullingReason: ' ',
      cullingType: 'padej',
      weight: '',
      needToRefresh: false,
    }
    this.cullingSow = this.cullingSow.bind(this);
    this.abortionSow = this.abortionSow.bind(this);

  }
  
  componentDidMount() {
    this.props.sowsResetErrorsAndMessages()
    this.props.getSowCullings({ws_number: 3})
    this.props.setSow(null)
  }


  cullingSow() {
    this.props.cullingSow(this.props.form.values)
    this.props.setSow(null)
    this.setState({
      ...this.state,
      needToRefresh: true
    })
  }

  abortionSow() {
    let data = {
      id: this.props.sow.id,
    }
    this.props.abortionSow(data)
    this.props.cullingSowFormResetID()
    this.props.setSow(null)
    this.setState({
      ...this.state,
      needToRefresh: true
    })
  }

  refreshSowsList () {
    if (this.props.eventFetching && this.state.needToRefresh) {
      setTimeout(() => {
        this.setState({...this.state, needToRefresh: false})
        this.props.getSowCullings({ws_number: 3})
      }, 300)
    }
  }

  render() {
    this.refreshSowsList()
    const { sow, eventError, message, eventFetching, cullings, singleSowFetching, errorSingle} = this.props
    return (
      <div className=''>
        <div className="modal fade" id="cullingModal" tabindex="-1" role="dialog" 
          aria-labelledby="cullingModalLabel" aria-hidden="true">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="cullingModalLabel"> </h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body" style={{"font-size": "1.2rem"}}>
                  <span style={{"font-weight": "bold"}}>{sow && sow.farm_id}.</span>
                <br/>
                 : {this.state.cullingType}.
                <br/>
                : {this.state.cullingReason}.
                <br/>
                : {this.state.weight}.
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-secondary" data-dismiss="modal"></button>
                <button type="button" className="btn bg-red1-light" data-dismiss="modal" 
                  onClick={this.cullingSow}>
                  
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* <div className='card my-2 mx-1'>
          <div className='content my-2'>
            <h4 className='mt-2 mx-2 mb-1'>  </h4>
            <input type="number" className="" placeholder=" "
              name='farm_id' onBlur={this.findSow} onChange={() => this.props.sowsResetErrorsAndMessages()}
              />
            {sow &&
                <SowSingle sow={sow} className='my-0 font-17 font-600 color-mainDark-dark'/>
              }
            {errorSingle && <p className='my-0 color-red1-light'>
                    ID    .</p>}
          </div>
        </div> */}

        <div className='card my-2 mx-1'>
          <div className='content'>
            <CullingSowForm
              parentSubmit={this.cullingSow}
              sow={sow}
              cullingTypes={[{value: 'padej', label: ''}, {value: 'vinuzhd', label: ' '},]}
              initialValues={{
                date: getDateTimeNow(),
                reason: ' ',
                weight: 0
              }}
              abort={this.abortionSow}
              eventError={eventError}
              eventFetching={eventFetching}
              message={message}
            />
            {/* <select className="font-16 py-2 my-2 mx-2" 
              name='cullingType' 
              onChange={this.setData}>
                <option selected value='padej' ></option>
                <option value='vinuzhd' > </option>
            </select>

            <input className="font-16 py-1 my-2 mx-2"
              type='text' onChange={this.setData} name='cullingReason'
              placeholder=' ' value={this.state.cullingReason}/>

            <input className="font-16 py-1 my-2 mx-2"
              type='number' onChange={this.setData} name='weight' value={this.state.weight}
              placeholder=' '/>

          </div>
          <div className='content mt-1'>
            <button type="button" className="btn bg-mainDark-dark ml-1 mr-3" data-toggle="modal"
                data-target="#cullingModal">
              
            </button>
            { this.props.abort &&
              <button className="btn bg-mainDark-dark ml-5" type="button"  
                onClick={this.abortionSow}>
                  
              </button>
            }
            <ErrorOrMessage error={eventError} message={message} fetching={eventFetching}
              className='my-3 mx-2 font-15' /> */}
          </div>
      </div>
      
      {singleSowFetching 
          ? <LoadingMessage />
          :cullings.length > 0 && 
        <div className='card my-2'>
          <div className='content'>
            <p className='my-1'> 10  </p>
            <table className='table table-sm'>
              <thead className='bg-mainDark-dark'>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </thead>
              <tbody>
                {cullings.map(c =>
                  <tr>
                    <td>{c.date}</td>
                    <td>{c.farm_id}</td>
                    <td>{c.culling_type}</td>
                    <td>{c.reason}</td>
                    <td>{c.weight}</td>
                    <td>{c.initiator}</td>
                  </tr>
                  )}
              </tbody>
            </table>
          </div>
        </div>}
    </div>
    )
  }
}

export default WSSowCullingTab