import React, { Component  } from 'react';

// components
import { PVEFilterForm } from './VetForms';
import { FetchingErrorMessage } from '../CommonComponents';


export default class VetEventsTab extends Component {
  constructor(props) {
    super(props);
    this.state = {
    }
    this.showEvents = this.showEvents.bind(this);
    this.deleteEvent = this.deleteEvent.bind(this);
  }

  componentDidMount() {
    this.props.getEvents()
    this.props.getFiltersData()
    this.props.resetEM()
  }

  showEvents () {
    this.props.resetEM()
    this.props.getEvents(this.props.form.values)
  }

  deleteEvent (e) {
    this.props.deletePigletsEvent({id: e.target.dataset.vid})
  }

  render() {
    const { vetEvents, filtersData, pveFormSetSections, eventFetching, eventError, message,
      listFetching } = this.props

    return (
      <div className='card card-style mt-1 mx-0'>
        <div className='content'>
          <h5>  </h5>
          <PVEFilterForm 
            parentSubmit={this.showEvents}
            filtersData={filtersData} 
            pveFormSetSections={pveFormSetSections}
           />
        </div>
        <div className='content my-0'>
          <FetchingErrorMessage fetching={listFetching} error={eventError} message={message}/>
          {vetEvents.length > 0
            ? <table className='table table-sm' style={{'line-height': '14px'}}>
                <thead>
                  <th>/</th>
                  <th></th>
                  <th>/- </th>
                  <th>//</th>
                  <th></th>
                </thead>
                <tbody>
                  {vetEvents.map(vEvent =>
                    <tr className='mb-0 color-dark1-light'>
                      <td>
                        <p className='my-0 color-dark1-light'>{vEvent.recipe.ru_type}</p>
                        <p className='my-0 color-dark1-light'>{vEvent.date.split(' ')[0]}</p>
                      </td>
                      <td>{vEvent.location}</td>
                      <td>
                        <p className='my-0 color-dark1-light'>{vEvent.week_tour.slice(0,6)}</p>
                        <span className=''>{vEvent.piglets_quantity} </span>
                      </td>
                      <td>
                        <p className='my-0 color-dark1-light'>{vEvent.recipe.drug_name}</p>
                        <span className='mr-1'>{vEvent.recipe.ru_method}</span>
                        <span className=''>{vEvent.recipe.doze}</span>
                      </td>
                      <td className='btn btn-sx bg-red1-light' data-vid={vEvent.id} 
                        onClick={this.deleteEvent}>
                        
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            : <p className=''> </p>
          }
        </div>
        
      </div>
  )}
}