import React, { Component } from 'react';

// components
import  { RecipesTab } from './Recipes';
import  { DrugsTab } from './Drugs';
import CircularProgress from '@material-ui/core/CircularProgress';

import { ErrorOrMessage, FetchingErrorComponentMessage } from '../CommonComponents';


class CreateRecipesAndDrugsTab extends Component {
   constructor(props) {
    super(props);
    this.state = {
      recipesActive: true,
      needToRefresh: false,
    }
    this.submitCreateRecipe = this.submitCreateRecipe.bind(this);
    this.submitUpdateRecipe = this.submitUpdateRecipe.bind(this);
    this.submitDeleteRecipe = this.submitDeleteRecipe.bind(this);

    this.submitCreateDrug = this.submitCreateDrug.bind(this);
    this.submitUpdateDrug = this.submitUpdateDrug.bind(this);
    this.submitDeleteDrug = this.submitDeleteDrug.bind(this);

    this.switchTab = this.switchTab.bind(this);
  }
  
  componentDidMount() {
    this.props.resetEM()
    this.props.getRecipes()
    this.props.getDrugs()
  }

  submitCreateRecipe () {
    this.props.createRecipe(this.props.createRecipeForm.values)
  }

  submitUpdateRecipe () {
    this.props.updateRecipe(this.props.updateRecipeForm.values)
  }

  submitDeleteRecipe (id) {
    this.props.deleteRecipe({id: id})
  }

  submitCreateDrug () {
    this.props.createDrug(this.props.createDrugForm.values)
  }

  submitUpdateDrug () {
    this.props.updateDrug(this.props.updateDrugForm.values)
  }

  submitDeleteDrug (id) {
    this.props.deleteDrug({id: id})
  }

  switchTab () {
    this.props.resetEM()
    this.setState({...this.state, recipesActive: !this.state.recipesActive})
  }

  render() {
    const { recipes, recipe, drugs, drug, listFetching, errorList, eventFetching, eventError, message } =
      this.props

    return (
      <div className=''>
        <div className='row mb-0 mt-1'>
          <div className='col-6'>
            <p className='btn btn-full mb-0 bg-mainDark-light' 
              onClick={this.switchTab}>
              
            </p>
          </div>
          <div className='col-6'>
            <p className='btn btn-full mb-0 bg-mainDark-light' 
              onClick={this.switchTab}>
                
            </p>
          </div>
        </div>
        {this.state.recipesActive 
          ? <RecipesTab 
              recipes={recipes} 
              recipe={recipe}
              drugs={drugs}

              listFetching={listFetching}
              errorList={errorList}
              eventFetching={eventFetching}
              eventError={eventError}
              message={message}

              submitCreateRecipe={this.submitCreateRecipe}
              submitUpdateRecipe={this.submitUpdateRecipe}
              submitDeleteRecipe={this.submitDeleteRecipe}
            />
          : <DrugsTab 
              drugs={drugs}

              listFetching={listFetching}
              errorList={errorList}
              eventFetching={eventFetching}
              eventError={eventError}
              message={message}

              submitCreateDrug={this.submitCreateDrug}
              submitUpdateDrug={this.submitUpdateDrug}
              submitDeleteDrug={this.submitDeleteDrug}
            />
        }
      </div>
    )
  }
}

export default CreateRecipesAndDrugsTab