import React, { Component } from 'react';
import { Link } from 'react-router-dom';


const MainMenuLink = (props) => (
    <div className="col-3 ws-home">
      <Link to={props.url}>{props.title}</Link>
    </div>
)

class MainPage extends Component {
   constructor(props) {
    super(props);
  }
  
  render() {
    
    return (
        <div className='main-page container-fluid'>
          <h1> </h1>
          <div className="row">
            <MainMenuLink url={'/workshop1/'} title={'1 '}/>
            <MainMenuLink url={'/workshop2/'} title={'2  '}/>
            <MainMenuLink url={'/workshop3/'} title={'3 '}/>
            <MainMenuLink url={'/workshop4/'} title={'4 '}/>
            <MainMenuLink url={'/workshop8/'} title={'8 '}/>
            <MainMenuLink url={'/workshop5/'} title={'5 '}/>
            <MainMenuLink url={'/workshop6/'} title={'6 '}/>
            <MainMenuLink url={'/workshop7/'} title={'7 '}/>
            <MainMenuLink url={'/workshop_boar/'} title={''}/>
            <MainMenuLink url={'/veterinary/'} title={''}/>
            <MainMenuLink url={'/reports/'} title={''}/>
          </div>
        </div>
    )
  }
}

export default MainPage