import React, { Component, } from 'react';
import { Field, reduxForm, } from 'redux-form';

import {  renderTextField, renderSelectField, renderDateTimeField, renderChildrenSelectField } 
  from '../PigletsTabs/PigletsForms'
import { makeStyles } from '@material-ui/core/styles';

import  { lodashToggle, uniq } from '../utils';
import { ErrorOrMessage, FetchingErrorMessage } from '../CommonComponents';


function RecipeForm(props) {
  const { med_types, med_methods, drugs } = props
  return (
    <div className='mb-4'>
      <Field 
        component={renderChildrenSelectField}
        name='drug'
        label=''
        className=''
      >
        {drugs.map(drug =>
          <option value={drug.id}>{drug.name}</option>
          )}
      </Field>

      <Field 
        component={renderChildrenSelectField}
        name='med_type'
        label=''
        className=''
      >
        {med_types.map(med_type =>
          <option value={med_type.value}>{med_type.label}</option>
          )}
      </Field>

      <Field 
        component={renderChildrenSelectField}
        name='med_method'
        label=''
        className=''
      >
        {med_methods.map(med_method =>
          <option value={med_method.value}>{med_method.label}</option>
          )}
      </Field>
        
      <Field 
        component={renderTextField}
        label="" 
        name='doze'
      />

      <Field
        component={renderTextField}
        name="id"
        hidden={true}
      />

    </div>
  )
}

const validateRecipeForm = values => {
  const errors = {}
  const requiredFields = [
    'drug',
    'med_type',
  ]
  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = ' '
    }
  })

  return errors
}

export function CreateRecipeForm (props) {
  const { med_types, med_methods, handleSubmit, parentSubmit, initialValues, pristine, submitting,
     drugs, eventFetching, eventError, message } = props
  return (
    <div className='card card-style mx-0 mb-1 mt-2'>
      <div className='content'>
        <form onSubmit={handleSubmit(parentSubmit)}>
          <RecipeForm initialValues={initialValues} drugs={drugs} med_types={med_types} 
            med_methods={med_methods}/>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-green2-light text-wrap'
            type="submit"
            disabled={pristine || submitting}>
             
          </button>
          <FetchingErrorMessage eventFetching={eventFetching}
              eventError={eventError}
              message={message}/>
        </form>
      </div>
    </div>
  )
}

CreateRecipeForm = reduxForm({
  form: 'createRecipeForm',
  validate: validateRecipeForm,
})(CreateRecipeForm)


